<script setup> 
import { ref } from "vue";
import axios from "axios";

var tasks = ref([]);
var selectedDateTime =[];
var remarks="";

axios
      .get('/tasks') 
      .then(response => {
        var temp = response.data;
        console.log(temp);
        let i = 0;
        for (let item of temp) {
            i=i+1;
            item.task_id = i;
            tasks.value.push(item);
        }
        // console.log(tasks);
        
      })


</script>
<template>
    <header class="overflow-x-auto font-extrabold text-6xl text-blue-900 py-8 bg-red-400 text-center">NLC INDIA LIMITED</header>
    <!-- <header class="overflow-x-auto font-extrabold text-blue-900 py-8 bg-red-400 text-center">ED Daily Schedule</header> -->
    <div class="overflow-x-auto">   
    <table class="w-full text-sm  rtl:text-right text-gray-800">
      <thead class="text-3xl text-yellow-800 uppercase bg-green-500">
        <tr class="">
          <th scope="col" c lass="px-20 py-3">
            S.No
          </th>
          <th scope="col" class="px-20 py-3">
            SUBJECT
          </th>
          <th scope="col" class="px-20 py-3">
             TIME
          </th>
          <th scope="col" class="px-20 py-3">
             REMARKS
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="task in tasks" :class=" [ {'even-group': task.task_id % 2 == 0},
                                            {'odd-group': task.task_id % 2 != 0} ]">
            <td class="px-20 py-3">
                {{ task.task_id }}
             </td>
          <td class="px-20 py-3">
            {{ task.task_subject }}
          </td>
          <td class="px-20 py-3">
            {{ task.task_time }}
          </td>
          <td class="px-20 py-3">
            {{ task.task_remarks }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<style>

tr.odd-group {
  background: rgb(215, 215, 215);
  border-bottom-width: 1px;
  color: rgb(54, 69, 79);
  /* font-weight: bold; */
  font-size: 20px;
  /* bg-gray-50 border text-center text-xl font-semibold text-gray-800 */
}   
tr.even-group {
  background: rgb(135, 206, 250);
  border-bottom-width: 1px;
  color: rgb(54, 69, 79);
  /* font-weight: bold; */
  font-size: 20px;
}
</style>